import React, {useEffect, useState} from "react";
import {useLocation, useNavigate} from "react-router-dom";

function WorkScheduleDashboard (){
    const [item, setItem] = useState(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState("");
    const [isEditing, setIsEditing] = useState(false);
    const [editedItem, setEditedItem] = useState({});
    const navigate = useNavigate();
    const location = useLocation();

    useEffect(() => {
        const schedule = location.state;
        if (schedule) {
            setIsEditing(schedule.stateInfo);
            // setEditedItem(location.state.item);
        }
    }, [location.state]);

    function handleClick() {
        navigate("/workSchedule/list");
    }

    return (
            <div className="detail-container mt-4">
                    <div className="card">
                        {/*<form onSubmit={handleClick}>*/}
                            <div className="card-header">
                                <h3>근무표 마이페이지</h3>
                            </div>
                            <div className="card-body">
                                <div>
                                    <div className="form-group">
                                        <label className="label">ID</label>
                                        <div>
                                            <input
                                                className="input"
                                                type="text"
                                                name="id"
                                                value="{editedItem.id}"
                                                // onChange={handleChange}
                                                disabled
                                            />
                                        </div>
                                    </div>
                                    <div className="form-group">
                                        <label className="label">이름</label>
                                        <div>
                                            <input
                                                className="input"
                                                type="text"
                                                name="name"
                                                value="{editedItem.name}"
                                                // onChange={handleChange}
                                                disabled
                                            />
                                        </div>
                                    </div>
                                    <div className="form-group">
                                        <label className="label">근무지</label>
                                        <div>
                                            <input
                                                className="input"
                                                type="text"
                                                name="workLocation"
                                                value="{editedItem.workLocation}"
                                                // onChange={handleChange}
                                            />
                                        </div>
                                    </div>
                                    <div className="form-group">
                                        <label className="label">기준 시간</label>
                                        <div>
                                            <input
                                                type="number"
                                                className="input"
                                                name="checkInTime"
                                                value="120"
                                                // value="{editedItem.basicWorkTime}"
                                            />
                                        </div>
                                    </div>
                                    <div className="form-group">
                                        <label className="label">기준 출근 시간</label>
                                        <div>
                                            <input
                                                type="time"
                                                className="input"
                                                name="checkInTime"
                                                value="00:00"
                                                // value="{editedItem.checkInTime}"
                                            />
                                        </div>
                                    </div>
                                    <div className="form-group">
                                        <label className="label">기준 퇴근 시간</label>
                                        <input
                                            type="time"
                                            className="input"
                                            name="checkOutTime"
                                            value="00:00"
                                            // value="{editedItem.checkOutTime}"
                                        />
                                    </div>
                                    <div className="form-group">
                                        <label className="label">기준 휴게시간</label>
                                        <input
                                            type="time"
                                            className="input"
                                            name="breakTimeIn"
                                            value="00:00"
                                            // value="{editedItem.breakTimeIn}"
                                        />
                                        ~
                                        <input
                                            type="time"
                                            className="input"
                                            name="breakTimeOut"
                                            value="00:00"
                                            // value="{editedItem.breakTimeOut}"
                                        />
                                    </div>
                                </div>
                            </div>
                            <div className="card-footer">
                                <button type="button" className="btn btn-secondary" onClick={handleClick}>
                                    {isEditing ? "수정하기" : "저장"}
                                </button>
                                {isEditing ? (
                                    <button type="button" className="btn btn-danger" onClick={handleClick}>
                                        취소
                                    </button>
                                ) : (
                                    <>
                                    </>
                                )
                                }
                            </div>
                        {/*</form>*/}
                    </div>
            </div>
    );
}
export default WorkScheduleDashboard;