import React, {useEffect, useState} from "react";
import {useLocation, useNavigate} from "react-router-dom";

function WorkScheduleDashboard (){
    const [item, setItem] = useState(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState("");
    const [isEditing, setIsEditing] = useState(false);
    const [editedItem, setEditedItem] = useState({
        checkInTime: "00:00",
        checkOutTime: "00:00",
        breakTimeIn: "00:00",
        breakTimeOut: "00:00",
        workType: "현장",
        attendanceType: "출근",
        memo: "메모를 작성해 주세요"
    });
    const navigate = useNavigate();
    const location = useLocation();
    
    function handleClick() {
        navigate("/workSchedule/list");
    }

    const handleChange = (e) => {
        const { name, value } = e.target;
        setEditedItem((prev) => ({
            ...prev,
            [name]: value
        }));
    };

    return (
            <div className="detail-container mt-4">
                    <div className="card">
                        {/*<form onSubmit={handleClick}>*/}
                            <div className="card-header">
                                <h3>근무표 상세 페이지</h3>
                            </div>
                            <div className="card-body">
                                <div>
                                        <div className="form-group">
                                            <label className="label">출퇴근 시간</label>
                                            <div className="d-flex">
                                                <input
                                                    type="time"
                                                    className="input "
                                                    name="checkInTime"
                                                    onChange={handleChange}
                                                    value={editedItem.checkInTime}
                                                />
                                                <h2>
                                                    ~
                                                </h2>
                                                <input
                                                    type="time"
                                                    className="input"
                                                    name="checkOutTime"
                                                    onChange={handleChange}
                                                    value={editedItem.checkOutTime}
                                                />
                                            </div>
                                        </div>
                                    <div className="form-group">
                                        <label className="label">휴게시간</label>
                                        <div className="d-flex">
                                            <input
                                                type="time"
                                                className="input"
                                                name="breakTimeIn"
                                                onChange={handleChange}
                                                value={editedItem.breakTimeIn}
                                            />
                                            <h2>
                                                ~
                                            </h2>
                                            <input
                                                type="time"
                                                className="input"
                                                name="breakTimeOut"
                                                onChange={handleChange}
                                                value={editedItem.breakTimeOut}
                                            />
                                        </div>
                                    </div>
                                    <div className="form-group">
                                        <label className="label">근무 유형</label>
                                        <select
                                            name="workType"
                                            value={editedItem.workType}
                                            onChange={handleChange}
                                            className="input"
                                            required
                                        >
                                            <option value="" disabled>
                                                근무 유형을 선택해 주세요.
                                            </option>
                                            <option value="현장">현장</option>
                                            <option value="재택근무">재택근무</option>
                                            <option value="본사">본사</option>
                                        </select>
                                    </div>
                                    <div className="form-group">
                                        <label className="label">근태 유형</label>
                                        <select
                                            name="attendanceType"
                                            value={editedItem.attendanceType}
                                            onChange={handleChange}
                                            className="input"
                                            required
                                        >
                                            <option value="" disabled>
                                                근태 유형을 선택해 주세요.
                                            </option>
                                            <option value="출근">출근</option>
                                            <option value="결근">결근</option>
                                            <option value="대휴">대휴</option>
                                            <option value="유급휴가">유급휴가</option>
                                            <option value="***">***</option>
                                        </select>
                                    </div>
                                    {editedItem.isHoliday !== "출근" && (
                                        <div className="form-group">
                                            <label> 사유를 적어주세요.</label>
                                            <input
                                                name="conversionDate"
                                                value={editedItem.memo}
                                                onChange={handleChange}
                                                required
                                                className="input"
                                            />
                                        </div>
                                    )}
                                </div>
                            </div>
                            <div className="card-footer">
                                <button type="button" className="btn btn-primary me-4" onClick={handleClick}>
                                    수정하기
                                </button>

                                <button type="button" className="btn btn-danger" onClick={handleClick}>
                                    취소
                                </button>
                            </div>
                        {/*</form>*/}
                    </div>
            </div>
    );
}
export default WorkScheduleDashboard;